import "./App.css";
import { useEffect, useState } from "react";
import { Container_Weight_Size } from "./config/config";
import {
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
} from "@mui/material";
import { CopyToClipboard } from "react-copy-to-clipboard";

