// console.log("결과값은??", result2);

const packingSites = ["CTRN"];
const pallets = ["Customized", "US Pallet"];
const products = ["Remsima", "Regkirona", "Truxima", "Herzuma", "CT-P17 SC"];
const dosages = [
  "100mg",
  "100mg(x3)",
  "100mg(x5)",
  "960mg",
  "500mg",
  "420(440)mg",
  "150mg",
  "60mg",
  "1L",
  "2L",
  "10L",
];
const types = ["uDP", "fDP", "fDP with BWFI", "fDP without BWFI", "DS"];

function App() {
  const [qty, setQty] = useState(0);
  const [percent, setPercent] = useState(0);
  const [container, setContainer] = useState(0);
  const [quantity, setQuantity] = useState(0);
  const [isCopied, setIsCopied] = useState(false);
  const [containerCopied, setContainerCopied] = useState(false);
  const [lastContainer, setLastContainer] = useState(0);

  const [inputs, setInputs] = useState({
    packingSiteValue: "",
    palletValue: "",
    productValue: "",
    dosageValue: "",
    typeValue: "",
  });

  const {
    packingSiteValue,
    palletValue,
    productValue,
    dosageValue,
    typeValue,
  } = inputs;

  const valueCondition =
    packingSiteValue !== "" &&
    palletValue !== "" &&
    productValue !== "" &&
    dosageValue !== "" &&
    typeValue !== "" &&
    quantity !== 0;

  const handleChange = (e) => {
    const { value, name } = e.target;
    setInputs({
      ...inputs,
      [name]: value,
    });
  };

  const handleResult = () => {
    if (quantity !== 0 && qty !== 0) {
      const containerNum = quantity / qty;
      const calculatedNum = quantity / (qty * Math.ceil(containerNum));
      const lastQty = quantity - qty * Math.trunc(containerNum);

      setContainer(containerNum.toFixed(2));
      setLastContainer(lastQty);
      setPercent(calculatedNum.toFixed(2));
    }
  };

  useEffect(() => {
    handleResult();
  }, [qty]);

  const handleClick = () => {
    // console.log(packingSiteValue);
    // console.log(palletValue);
    // console.log(productValue);
    // console.log(dosageValue);
    // console.log(typeValue);
    // console.log("출하량?", quantity);
    // console.log("QTY는:??", qty);
    const filterdQty = Container_Weight_Size.filter(
      (obj) =>
        obj.package_site === packingSiteValue &&
        obj.pallets === palletValue &&
        obj.product === productValue &&
        obj.dosage === dosageValue &&
        obj.type === typeValue
    );
    if (filterdQty && filterdQty.length === 1) {
      setQty(filterdQty[0].QTY);
      handleResult();
    } else if (filterdQty.length === 0) {
      alert("조건에 부합하는 값이 존재하지 않습니다");
      // 결과값 리셋
      setQty(0);
      setPercent(0);
      setQuantity(0);
      setContainer(0);
      setLastContainer(0);
    }
  };

  const onCopyText = () => {
    setIsCopied(true);
    setTimeout(() => {
      setIsCopied(false);
    }, 1300);
  };

  const onContainerCopyText = () => {
    setContainerCopied(true);
    setTimeout(() => {
      setContainerCopied(false);
    }, 1300);
  };

  return (
    <>
      <div className="App">
        <h2>적재율 계산기</h2>
        <div className="form-container">
          <div className="input-container">
            <FormControl sx={{ m: 1, minWidth: 120, width: 150 }}>
              <InputLabel>Package Site</InputLabel>
              <Select
                onChange={(e) => handleChange(e)}
                value={packingSiteValue}
                name="packingSiteValue"
                label="Package Site"
              >
                {packingSites.map((site, index) => (
                  <MenuItem value={site} key={index}>
                    {site}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl
              sx={{ m: 1, minWidth: 120 }}
              disabled={!packingSiteValue}
              className="option-container"
            >
              <InputLabel>Pallets</InputLabel>
              <Select
                onChange={(e) => handleChange(e)}
                value={palletValue}
                name="palletValue"
                label="Pallets"
              >
                {pallets.map((pallet, index) => (
                  <MenuItem value={pallet} key={index}>
                    {pallet}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl
              sx={{ m: 1, minWidth: 120 }}
              className="option-container"
              disabled={!packingSiteValue || !palletValue}
            >
              <InputLabel>Product</InputLabel>
              <Select
                onChange={(e) => handleChange(e)}
                value={productValue}
                name="productValue"
                label="Product"
              >
                {products.map((product, index) => (
                  <MenuItem value={product} key={index}>
                    {product}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl
              sx={{ m: 1, minWidth: 120 }}
              className="option-container"
              disabled={!packingSiteValue || !palletValue || !productValue}
            >
              <InputLabel>Dosage</InputLabel>
              <Select
                onChange={(e) => handleChange(e)}
                value={dosageValue}
                name="dosageValue"
                label="Dosage"
              >
                {dosages.map((dosage, index) => (
                  <MenuItem value={dosage} key={index}>
                    {dosage}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl
              sx={{ m: 1, minWidth: 120 }}
              className="option-container"
              disabled={
                !packingSiteValue ||
                !palletValue ||
                !productValue ||
                !dosageValue
              }
            >
              <InputLabel>Type</InputLabel>
              <Select
                onChange={(e) => handleChange(e)}
                value={typeValue}
                name="typeValue"
                label="Type"
              >
                {types.map((type, index) => (
                  <MenuItem value={type} key={index}>
                    {type}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            <TextField
              type="number"
              placeholder="출하량"
              value={quantity}
              onChange={(e) => setQuantity(e.target.value)}
              label="출하량"
            />
          </div>
          <button onClick={handleClick} disabled={!valueCondition}>
            계산하기
          </button>
        </div>

        <div className="calculated-container">
          <h3>계산결과</h3>
          <div className="result-container">
            <TableContainer>
              <Table sx={{ minWidth: 150 }} aria-label="simple table">
                <TableHead>
                  <TableRow className="table-header">
                    <TableCell align="center" width={170}>
                      항목
                    </TableCell>
                    <TableCell align="center">결과값</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  <TableRow>
                    <TableCell scope="row" align="center">
                      QTY
                    </TableCell>
                    <TableCell scope="row" align="center">
                      {qty}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell scope="row" align="center">
                      필요 컨테이너수(대)
                    </TableCell>
                    <TableCell scope="row" align="center">
                      <span>{container}</span>
                      {container !== 0 ? (
                        <CopyToClipboard
                          text={container}
                          onCopy={onContainerCopyText}
                        >
                          <span className="copy-button">
                            {!containerCopied ? "Copy" : "Copied"}
                          </span>
                        </CopyToClipboard>
                      ) : null}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell scope="row" align="center">
                      마지막 컨테이너 적재량
                    </TableCell>
                    <TableCell scope="row" align="center">
                      {lastContainer}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell scope="row" align="center">
                      적재율
                    </TableCell>
                    <TableCell scope="row" align="center" className="last-cell">
                      <span>{percent}</span>
                      {percent !== 0 ? (
                        <CopyToClipboard text={percent} onCopy={onCopyText}>
                          <span className="copy-button">
                            {!isCopied ? "Copy" : "Copied"}
                          </span>
                        </CopyToClipboard>
                      ) : null}
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>
          </div>
        </div>
      </div>
    </>
  );
}

export default App;
